import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
const authModule = () => import('./modules/auth/auth.module').then(x => x.AuthModule);
const homeModule = () => import('./modules/home/home.module').then(x => x.HomeModule);

const routes: Routes = [
    { path: '', redirectTo: 'home', pathMatch: 'full' },
    { path: 'auth', pathMatch: 'prefix', loadChildren: authModule },
    { path: 'home', pathMatch: 'prefix', loadChildren: homeModule },
    // otherwise redirect to home
    { path: '**', redirectTo: '' }
];

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRoutingModule { }
